'use client'
import { createContext, ReactNode, useContext } from 'react'

const ClusterContext = createContext<{
  cluster: { network: string; endpoint: string }
}>({
  cluster: { network: 'localnet', endpoint: 'http://127.0.0.1:8899' } // Changed from localhost to 127.0.0.1
})

export function ClusterProvider({ children }: { children: ReactNode }) {
  const cluster = { network: 'localnet', endpoint: 'http://127.0.0.1:8899' } // Changed from localhost to 127.0.0.1
  
  return (
    <ClusterContext.Provider value={{ cluster }}>
      {children}
    </ClusterContext.Provider>
  )
}

export function useCluster() {
  return useContext(ClusterContext)
}