'use client'

import { useState, useMemo } from 'react'
import { useWallet } from '@solana/wallet-adapter-react'
// Remove this import since you have your own wallet button
import { Button } from '@/components/ui/button'
import { useConnection } from '@solana/wallet-adapter-react'
import { LAMPORTS_PER_SOL } from '@solana/web3.js'
import { Crown, Menu, X, Zap } from 'lucide-react'
import { useCoinFlipProgram } from '../coin-flip/coin-flip-data-access'
import { useRouter } from 'next/navigation'
import { toast } from 'sonner'

export function Header() {
  const { connection } = useConnection()
  const wallet = useWallet()
  const router = useRouter()
  const { accounts } = useCoinFlipProgram()
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [isAirdropping, setIsAirdropping] = useState(false)

  // Check if connected wallet owns any game state
  const isOwner = useMemo(() => {
    if (!wallet.publicKey || !accounts.data) return false
    return accounts.data.some((account: any) => 
      account.account.owner.equals(wallet.publicKey!)
    )
  }, [wallet.publicKey, accounts.data])

  const handleAirdrop = async () => {
    if (!wallet.publicKey) {
      toast.error('Please connect your wallet first')
      return
    }

    setIsAirdropping(true)
    try {
      const signature = await connection.requestAirdrop(
        wallet.publicKey,
        2 * LAMPORTS_PER_SOL
      )
      
      await connection.confirmTransaction(signature)
      toast.success('Airdrop successful! 2 SOL added to your wallet')
    } catch (error) {
      console.error('Airdrop failed:', error)
      toast.error('Airdrop failed. You may have reached the daily limit.')
    } finally {
      setIsAirdropping(false)
    }
  }

  const handleAdminAccess = () => {
    if (!wallet.publicKey) {
      toast.error('Please connect your wallet first')
      return
    }
    
    if (!isOwner) {
      toast.error('Access denied. Only game owners can access admin panel.')
      return
    }
    
    router.push('/admin')
  }

  return (
    <>
      <header className="bg-white shadow-sm border-b border-gray-200 sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            {/* Logo */}
            <div className="flex items-center">
              <button 
                onClick={() => router.push('/')}
                className="flex items-center space-x-2 hover:opacity-80 transition-opacity"
              >
                <div className="w-8 h-8 bg-gradient-to-r from-purple-500 to-blue-500 rounded-lg flex items-center justify-center">
                  <span className="text-white font-bold text-lg">J</span>
                </div>
                <span className="text-xl font-bold bg-gradient-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent">
                  JUMBA CREATIONS
                </span>
              </button>
            </div>

            {/* Desktop Navigation */}
            <div className="hidden md:flex items-center space-x-4">
              {/* Airdrop Button */}
              <Button
                onClick={handleAirdrop}
                disabled={isAirdropping || !wallet.publicKey}
                variant="outline"
                className="flex items-center gap-2"
              >
                <Zap className="h-4 w-4" />
                {isAirdropping ? 'Airdropping...' : 'Airdrop 2 SOL'}
              </Button>

              {/* Admin Button - Only show if user is owner */}
              {isOwner && (
                <Button
                  onClick={handleAdminAccess}
                  variant="outline"
                  className="flex items-center gap-2 border-purple-300 text-purple-700 hover:bg-purple-50"
                >
                  <Crown className="h-4 w-4" />
                  Admin
                </Button>
              )}

              {/* Your Custom Wallet Button - Replace this div with your wallet button component */}
              <div className="wallet-button-placeholder">
                {/* Replace this with your existing wallet button component */}
                <div className="px-4 py-2 bg-gradient-to-r from-green-500 to-emerald-500 text-white rounded-lg font-semibold">
                  <WalletButton/>
                </div>
              </div>
            </div>

            {/* Mobile menu button */}
            <div className="md:hidden">
              <button
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                className="p-2 rounded-md text-gray-700 hover:bg-gray-100"
              >
                {isMenuOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
              </button>
            </div>
          </div>
        </div>

        {/* Mobile Navigation */}
        {isMenuOpen && (
          <div className="md:hidden border-t border-gray-200 bg-white">
            <div className="px-4 py-4 space-y-3">
              <Button
                onClick={handleAirdrop}
                disabled={isAirdropping || !wallet.publicKey}
                variant="outline"
                className="w-full flex items-center gap-2"
              >
                <Zap className="h-4 w-4" />
                {isAirdropping ? 'Airdropping...' : 'Airdrop 2 SOL'}
              </Button>

              {isOwner && (
                <Button
                  onClick={handleAdminAccess}
                  variant="outline"
                  className="w-full flex items-center gap-2 border-purple-300 text-purple-700 hover:bg-purple-50"
                >
                  <Crown className="h-4 w-4" />
                  Admin Panel
                </Button>
              )}

              <div className="pt-2">
                {/* Replace this with your existing wallet button component */}
                <div className="w-full px-4 py-2 bg-gradient-to-r from-green-500 to-emerald-500 text-white rounded-lg font-semibold text-center">
                  Your Wallet Button Here
                </div>
              </div>
            </div>
          </div>
        )}
      </header>
    </>
  )
}